
body {
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

header {
  width: 100%;
  text-align: center;
  background-color:white;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  position: relative;
  top: 0px;
  z-index: 10;
  font-family: cursive;
  text-shadow: 0px 0.3px 0.1px #999999;
}
a {
  height: 60px;
  line-height: 60px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 18px;
  color: black;
  text-decoration: none;
  width: 150px;
  display: inline-block;
&:hover{
    background-color: darken(white,5%);
}
}
.clear {
  clear: left;
}

#hello {
  position: relative;
  height: 96vh;
  text-align: center;
  color: white;
  font-family: cursive;
  background: linear-gradient(-45deg, rgba(246, 255, 0, .8), rgba(255, 0, 161, .8)),
  url(/vue-portfolio/static/img/sakura.6a74cf2.jpg);
  background-size: cover;
  text-shadow: 0px 4px 3px #999999;
}
#hello h1 {
  padding-top: 35vh;
  font-size: 50px;
  letter-spacing: 3px;
  font-weight: normal;
  padding-right : 20px;
  padding-left : 20px;
  margin: 0;
}
#hello h2 {
  padding-top: 10vh;
  font-size: 25px;
}
.v-enter-active {
  -webkit-transition: opacity 1.5s;
  transition: opacity 1.5s;
}
.v-enter {
  opacity: 0;
}
@media (max-width : 670px){
#hello {
    height: 70vh;
    font-family: 'arial unicode ms';
}
#hello h1 {
    padding-top: 17vh;
    font-size: 30px;
}
#hello h2 {
    padding-top: 10vh;
    font-size: 20px;
}
}

#prof {
  position: relative;
  color: #222222;
  text-align: center;
  font-family: 'arial unicode ms';
  text-shadow: 0px 0.3px 0.1px #999999;
}
#prof p {
  margin-top: 0px;
}
#picture {
  margin: 30px auto;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  background: url(data:image/gif;base64,);
  background-size: contain;
  border: solid 1px #c0c0c0;
}
#prof table {
  padding: 0px 70px;
  font-size: 18px;
  display: inline-block;
  text-align: left;
}
#prof table th {
  padding-right: 20px;
  font-weight: normal;
}
#prof table td {
  padding : 5px 0px;
}
#prof table td a {
  margin: 0px;
  height: 25px;
  font-size: 18px;
  color: #6495ed;
  line-height: 25px;
}
#prof .heading {
  padding-top: 5vh;
  font-size: 22px;
  font-weight: bold;
}
#prof .goal {
  font-size: 18px;
}
#prof .eng {
  font-family: cursive;
}
#prof #feature ul {
  width: 650px;
  margin: 0px auto;
  text-align: left;
}
#prof #feature ul li {
  margin: 15px 0px;
}
#prof #license {
  margin-bottom: 20px;
}
.v-enter-active {
  -webkit-transition: opacity 1.5s;
  transition: opacity 1.5s;
}
.v-enter {
  opacity: 0;
}
@media (max-width : 670px){
#prof {
    font-family: 'arial unicode ms';
}
#prof #feature ul {
    width: 85%;
    width: 80%;
    font-size: 14px;
}
#license {
    display: inline-block;
}
}

.pokemon {
  margin: 10px 0px 40px 0px;
}
#ash,
#pikachu {
  position: relative;
  display: inline-block;
  margin: 0 10px;
  overflow: hidden;
}
#ash:before, #ash:after,
#pikachu:before,
#pikachu:after {
  content: " ";
  position: absolute;
  width: 4px;
  height: 4px;
  top: 0;
  left: 0;
}
#ash:before,
#pikachu:before {
  margin-left: 120px;
}
#ash {
